import {Link} from 'react-router-dom'
export default function Layout(props){

    function tocheck(obj){
        
        if(obj.image==null){
          return "null";
        }else{
          return obj.image.medium;
        }
       }
    return(
        <div className="App">
          <section key={props.show.id}>
            <img className="app-image" src={tocheck(props.show)}alt="no img"></img>
            <h1 className="app--showName">{props.show.name}</h1>
            <h3 className="app--showLang">{props.show.language}</h3>
            <h3 className="app--showType">{props.show.type}</h3>
            {/* <button className="app--button" onClick={toggle}>Show Summary</button> */}
            <Link to={`/american-tvshow/summary/${props.show.id}`} key={props.show.id} className='app--button'>Show Summary</Link>
</section>
       
   </div>
    )
}