import { useParams } from "react-router-dom";
import {useState,createRef} from "react";
import parse from "html-react-parser";
export default function Summary(props){
    const[btnTxt,setBtnTxt]=useState('Book');
let firstname=createRef();
let lastname=createRef();
let usernumber=createRef() 
    let sh
const {id}=useParams();
for (let index = 0; index < props.tv.length; index++) {
    const element = props.tv[index];
    if(element.show.id===Number(id)){
        sh=element.show
    }
    }

    function tocheck(obj){
        
        if(obj.image==null){
          return "null";
        }else{
          return obj.image.medium;
        }
       }
       const changeText = (text) => setBtnTxt(text);
       function toggle(){
        localStorage.setItem("FirstName",(firstname.current.value));
        localStorage.setItem("LastName",(lastname.current.value));
        localStorage.setItem("UserNumber",(usernumber.current.value));
        document.getElementById("user").style.display="none"; 
        
    }

    return(
    <main className="Card">
       <div className="card--img"> <img src={tocheck(sh)} alt="no img" className="Card--image"></img></div>
        {/* <img alt="no im" srcSet="../images/star.png"> </img>  */}
        <div className="Card--section"><h4 className="Card--name">Name : {sh.name}</h4>
       <h6 className="Card--ratings">Ratings : {sh.rating.average}</h6>  
        
        <h6 className="Card--genres">Genres    :  {sh.genres}</h6>
        <h6 className="Card--language">Language  : {sh.language} </h6>
        <h6 className="Card--schedule">Schedule  :  {sh.schedule.time} , {sh.schedule.days}</h6>
        </div>
        <div className="Card--summary">Summary   :  {parse(sh.summary)}</div>
        <form className="Card--form" id="user">
            <h3 className="Card--form--title">Booking Details
            </h3>
            <div className="Card--form--inputSection">
            <label>First Name</label>
<input  ref={firstname}type="text" placeholder="First Name"/>
<label>Last Name</label>
<input ref={lastname} type="text" placeholder="Last Name"/>
<label>Mobile Number</label>
<input ref={usernumber}type="number" placeholder="Number"/>
</div>
</form>
{/* <button onClick={document.getElementById("userForm").style.display="block"}>Open Form</button> */}
<button className="Card--button"onClick={()=>{toggle() ;changeText("Booked")}}>{btnTxt}</button>
    </main>
)
}