import './App.css';
import { useState,useEffect } from 'react';
import { BrowserRouter,Routes,Route} from 'react-router-dom'
import Home from './components/Home';
import Summary from './components/Summary';


function App() {
  const [sho, setShow] = useState([]);
  const fetchData = () => {
    return fetch("https://api.tvmaze.com/search/shows?q=all")
          .then((response) => response.json())
          .then((data) => setShow(data));
  }

  useEffect(() => {
    fetchData();
  },[])
  
  return (
         <BrowserRouter>
         <Routes>
         <Route exact path='/american-tvshow' element={<Home tvshow={sho}/>}/>
         <Route path='/american-tvshow/summary/:id' element={<Summary tv={sho}/>}/>
         </Routes>
          </BrowserRouter>);
}

export default App;
